<template>
  <div class="customer-list">
    <router-link :to="{name: 'new-customer'}">New Customer</router-link>
    <h3>Customers</h3>
    <table class="table table-hover">
      <thead class="thead-light">
        <tr>
          <th>Name</th>
          <th>Email</th>
          <th>Phone</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for='customer in customers'
          v-bind:key='customer.id'
          class="customer-row"
          v-on:dblclick='openCustomer(customer.id)'>
          <td>{{customer.name}}</td>
          <td>{{customer.email}}</td>
          <td>{{customer.phone}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
 import db from './firebase-init'
 import router from '@/router'

 export default {
   name: 'customers',
   data() {
     return {
       customers: []
     }
   },
   created() {
     db.collection('customers').get().then(
       snap => {
        snap.forEach(doc => {
          const data = {
            'id': doc.id,
            'name': doc.data().name,
            'billing_address': doc.data().billing_address,
            'phone': doc.data().phone,
            'email': doc.data().email,
          }
          this.customers.push(data)
        })
      })
   },
   methods: {
     openCustomer(id) {
       router.push({ name: 'customer', params: { id }})
     }
   }
 }
</script>
